import { useEffect, useState } from "react";
import axios from "axios";
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import TextField from "@mui/material/TextField";import Box from '@mui/material/Box';
import { DataGrid } from "@mui/x-data-grid";
import styles from "./Rooms.module.css";

export default function Rooms(props) {
  const [rooms, setRooms] = useState([]);
  useEffect(() => {
    //fetch room list
  }, []);

  let fetchedRooms = <Grid container></Grid>;

  const [open, setOpen] = useState(false);

  const handlemakeroom = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  let roomcreate = (
    <Grid item xs={12}>
      <Button variant="primary" onClick={handlemakeroom}>
        
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <div>
            <TextField id="standard-basic" label="" variant="standard" />
          </div>
          <div>
            <TextField
              id="standard-basic"
              label=""
              variant="standard"
            />
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}></Button>
        </DialogActions>
      </Dialog>
    </Grid>
  );

  const columns = [
    { field: "id", headerName: " ", width: 90 },
    {
      field: "roomname",
      headerName: " ",
      width: 150,
      editable: false,
    },
    {
      field: "current_count",
      headerName: "",
      width: 150,
      editable: false,
    },
    {
      field: "max_count",
      headerName: "",
      type: "number",
      width: 110,
      editable: false,
    },
    {
      field: "password",
      headerName: " ",
      sortable: false,
      width: 160,
      editable: false,
    },
  ];

  const rows = [
    { id: 1, roomname: "1", current_count: 1, max_count: 6 , password: "" },
    { id: 2, roomname: "2", current_count: 1, max_count: 6 , password: "" },
    { id: 3, roomname: "3", current_count: 1, max_count: 6 , password: "" },
    
  ];

  let roomsdummy = (
    <Grid className={styles.container}>
      {roomcreate}
      <Link to="/game"> </Link>
      <Box sx={{ height: 400, width: "100%" }}>
        <DataGrid
          rows={rows}
          columns={columns}
          pageSize={5}
          rowsPerPageOptions={[5]}
          disableSelectionOnClick
        />
      </Box>
      
    </Grid>
  );

  return roomsdummy;
}
