import Nav from 'react-bootstrap/Nav';
import React, { useEffect, useState } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import styles from "./Tutorial.module.css";

function TabContent(props){
  if(props.clicked === 0){
    return <div className={styles.contents}>
      <p>   2     1,  3       .</p>
      <p>2         .<br/>
      1         ,        .</p>
      <p> 1 10 , , ,     .</p>
      <p>   2       ,         .<br/> 
                .</p>
    </div>
  }else if(props.clicked === 1) {
    return <div className={styles.contents}>
      <h3></h3>
      <p>     1   </p>
      <h3></h3>
      <p>  2   3    </p>
      <h3></h3>
      <p>  2   3    </p>
      <p>        .<br/>
          &#60;  &#60;  &#60;   .<br/>
         .</p>
    </div>
  }else if(props.clicked === 2){
    return <div className={styles.contents}>
      <p>     .<br/>
          .
      </p>
      <h3></h3>
      <p>     </p>
      <h3></h3>
      <p>    </p>
      <h3></h3>
      <p>      </p>
      <h3></h3>
      <p>    </p>
    </div>
  }else if(props.clicked === 3){
    return <div className={styles.contents}>
      <p>       .</p>
    </div>
  }
}

export default function Tutorial() {

  let [clicked, changeTab] = useState(0);
      return (
        <div className={styles.container}>
          <Nav variant="tabs" defaultActiveKey="link-0" className={styles.nav}>
            <Nav.Item className={styles.navItem}>
              <Nav.Link eventKey="link-0" onClick={()=>{changeTab(0)}} className={styles.navLink}></Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link-1" onClick={()=>{changeTab(1)}} className={styles.navLink}></Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link-2" onClick={()=>{changeTab(2)}} className={styles.navLink}></Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link-3" onClick={()=>{changeTab(3)}} className={styles.navLink}></Nav.Link>
            </Nav.Item>
          </Nav>
          <TabContent clicked={clicked} />
        </div>
      );
    }