import { useEffect, useState } from "react";
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";
import Paper from "@mui/material/Paper";
// import axios from "axios";
import styles from "./Logo.module.css";
import Login from '../../pages/Login/Login';
import Signup from '../../pages/Signup/Signup';
import Lobby from '../../pages/Lobby/Lobby'
import Game from '../../pages/Game/Game';;

export default function First(props) {
  useEffect(() => {}, []);

  let header = (
    <div className={styles.bg}>
      <div className={styles.mainframe}>
      <div className={styles.explan}>WebRTC    </div>
      <div className={styles.parent}><div className={styles.child}>Mentalist</div></div>
          <Link to="/login"><button className={styles.button}></button></Link><br/>
          <Link to="/signup"><button className={styles.button}></button></Link><br/>
          <Link to="/lobby"><button className={styles.button}> </button></Link>
      </div>
    </div>
  );

  return (
    <div>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={header}></Route>
          <Route path="/login" element={<Login />}></Route>
          <Route path="/signup" element={<Signup/>}></Route>
          <Route path="/lobby/*" element={<Lobby />}></Route>
          <Route path="/game" element={<Game />}></Route>
        </Routes>
      </BrowserRouter>
    </div>
  );

}

