import React from "react";
import axios from "axios";
import { useEffect, useState } from "react";
import { TextField } from "@material-ui/core";

export default function Vault() {
  const [vaultmoney, setVaultmoney] = useState();
  const [depositamount, setDepositamount] = useState();
  const [withdrawamount, setWithdrawamount] = useState();
  const [depositstatus, setDepositstatus] = useState("");
  const [withdrawstatus, setWithdrawstatus] = useState("");
  useEffect(() => {
    axios
      .get("http://localhost:8080/api/vault", {
        headers: {
          "Content-Type": "application/json",

          Authorization: window.localStorage.getItem("accessToken"),
        },
      })
      .then((response) => {
        console.log("vault balance = " + response.data);
        setVaultmoney(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  function Deposit() {
    console.log("", depositamount);
    axios
      .put("http://localhost:8080/api/vault/update", depositamount, {
        headers: {
          "Content-Type": "application/json",

          Authorization: window.localStorage.getItem("accessToken"),
        },
      })
      .then((response) => {
        console.log("vault balance = " + response.data.userVault);
        setVaultmoney(response.data.userVault);
        setDepositstatus(depositamount + "  ");
      })
      .catch((error) => {
        if (error.response.data.error == 10) console.log(" ");
        setDepositstatus(" ");
      });
  }

  function Withdraw() {
    console.log("" + withdrawamount);
    axios
      .put("http://localhost:8080/api/vault/update", withdrawamount * -1, {
        headers: {
          "Content-Type": "application/json",

          Authorization: window.localStorage.getItem("accessToken"),
        },
      })
      .then((response) => {
        console.log("vault balance = " + response.data.userVault);
        setVaultmoney(response.data.userVault);
        setWithdrawstatus(withdrawamount + "  ");
      })
      .catch((error) => {
        if (error.response.data.error == 20) {
          console.log(" ");
          setWithdrawstatus("  ");
        }
      });
  }

  return (
    <div>
      <p>  </p>
      <p>{vaultmoney}</p>

      <TextField
        onChange={(e) => {
          setDepositamount(e.target.value);
        }}
        autoFocus
      ></TextField>
      <button onClick={Deposit}></button>
      <p>{depositstatus}</p>
      <TextField
        onChange={(e) => {
          setWithdrawamount(e.target.value);
        }}
      ></TextField>
      <button onClick={Withdraw}></button>
      <p>{withdrawstatus}</p>
    </div>
  );
}
