import { useEffect, useState } from "react";
import axios from "axios";
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import TextField from "@mui/material/TextField";import Box from '@mui/material/Box';
import { DataGrid } from "@mui/x-data-grid";
import styles from "./Ranking.module.css";

export default function Ranking(props) {
  useEffect(() => {
    //fetch rank
  }, []);


  const columns = [
    {
      field: "id",
      headerName: "",
      width: 90
    },
    {
        field: "tier",
        headerName: "",
        width: 30,
        editable: false,
      },
    {
      field: "nickname",
      headerName: "",
      width: 150,
      editable: false,
    },
    {
        field: "badge",
        headerName: "",
        width: 30,
        editable: false,
      },
    {
      field: "ruby",
      headerName: " ",
      width: 150,
      editable: false,
    },
    {
        field: "guild",
        headerName: "",
        width: 150,
        editable: false,
      }
  ];

  const rows = [
    { id: 1, tier: "",nickname: "1", badge:"",ruby: 100, guild:"1"},
    { id: 2, tier: "",nickname: "2",  badge:"",ruby: 10, guild:"2"},
    { id: 3, tier: "",nickname: "3",  badge:"",ruby: 1, guild:"2"},
    
  ];

  let roomsdummy = (
    <Grid className={styles.container}>
      <Box sx={{ height: 400, width: "100%" }}>
        <DataGrid
          rows={rows}
          columns={columns}
          pageSize={5}
          rowsPerPageOptions={[5]}
          disableSelectionOnClick
        />
      </Box>
      
    </Grid>
  );

  return roomsdummy;
}
